<template>
  <div id="app" class="page-container">
    <md-app md-mode="fixed-last">
      <md-app-toolbar style="backgroundcolor: #0100FF;" class="md-large md-dense md-primary">
        <div class="md-toolbar-row">
          <span class="md-title">YUMI_managerpage</span>
        </div>
        <div style="flex:1">
          <div align="right">
            <input v-model="user.email" placeholder="ID" />
            <input v-model="user.password" placeholder="password" />
            <button v-on:click="login">login</button>
          </div>
        </div>
        <div class="md-toolbar-row" v-if="true">
          <md-tabs class="md-primary" style="margin: auto; height: 30px">
            <md-tab id="tab-dashBoard" md-label="문의사항 관리" to="/dashBoard"></md-tab>
            <md-tab id="tab-SupporterList" md-label="서포터즈 목록" to="/SupporterList"></md-tab>
            <md-tab id="tab-SupporterAssign" md-label="서포터즈 신청" to="/SupporterAssign"></md-tab>
          </md-tabs>
        </div>
        <div class="md-toolbar-row" v-else>
          <md-tabs class="md-primary" style="margin: auto; height: 30px">
            <md-tab id="tab-dashBoard" md-label="문의사항 관리"></md-tab>
            <md-tab id="tab-SupporterList" md-label="서포터즈 목록"></md-tab>
            <md-tab id="tab-SupporterAssign" md-label="서포터즈 신청"></md-tab>
          </md-tabs>
        </div>
      </md-app-toolbar>
    </md-app>
    <router-view />
  </div>
</template>

<script>
import Vue from "vue";
import VueMaterial from "vue-material";
import "vue-material/dist/vue-material.min.css";
import "vue-material/dist/theme/default.css";
import axios from "axios";
 
Vue.use(VueMaterial);

export default {
  name: "App",
  data() {
    return {
      isloggedin : false,
      user: {
        email: "",
        password: "",
      },
    };
  },
  created() {
        
          this
            .$http
            .get('/api/supporter/login/isloggedin')
            .then(response => {
              this.isloggedin = response.data.isloggedin; 
              console.log(this.isloggedin)
            })
          },

  methods: {
    login() {
      axios
        .post("/api/supporter/login/check", {
          email:this.user.email, password:this.user.password
        })
        .then(
          response => {
            //로그인 성공
            alert("success login");
            location.reload();
          },
          error => {
            // error 를 보여줌
            alert("login failed");
          }
        )
        .catch(error => {
          alert(error);
        });
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
}
</style>



